import { ErrorHandler } from './ErrorHandler';
import { httpClientErrorMappingSrc176Evid100 } from '@wix/error-handler-core/@wix/bi-logger-os-data/v2';
import { ErrorAccessor } from '../state/ErrorAccessor';
import { withoutDefaults } from '../util/withoutDefaults';
import { ErrorCodesMap } from '@wix/error-handler-core/types';

export interface ReportErrorBiParams {
  readonly errorAccessor: ErrorAccessor;
  readonly errorCodesMap: ErrorCodesMap | null | undefined;
}

export function reportErrorResolutionBi(
  errorHandler: ErrorHandler,
  params: ReportErrorBiParams,
) {
  const { errorAccessor, errorCodesMap } = params;
  const { biLogger } = errorHandler;
  const { errorHandlerState, applicationError } = errorAccessor;
  const code = applicationError?.code;
  const applicationErrorMapping =
    errorCodesMap?.applicationError?.[code as string];

  biLogger.report(
    withoutDefaults(httpClientErrorMappingSrc176Evid100)({
      error_opt_out: applicationErrorMapping === false,
      is_override_error: String(errorCodesMap != null),
      session_id: errorHandlerState?.sessionId,
    }),
  );
}
